import React, { useCallback } from 'react';

import ControlButton, { ControlButtonProps } from '../ControlButton';
import { FaMapMarkerAlt } from 'react-icons/fa';
import PinCoordinatesLib from './PinCoordinatesLib';
import { useMap } from '../../Hooks';

// import { useMap } from '../../Hooks';

export interface PinCoordinatesProps
  extends Omit<ControlButtonProps, 'loading' | 'activeMenuControl'> {
  controlKey?: string;
}

const PinCoordinates: React.FC<PinCoordinatesProps> = ({
  styled,
  activeLabel,
  icon,
  color,
  controlKey,
}) => {
  const { map } = useMap();

  const onEnable = useCallback(() => {
    if (!map) return;
    PinCoordinatesLib.startPinCoordinateInteraction(map);
  }, [map]);

  const onDisable = useCallback(() => {
    if (!map) return;
    PinCoordinatesLib.stopPinCoordinateInteraction(map);
  }, [map]);

  return (
    <ControlButton
      styled={styled}
      icon={icon || <FaMapMarkerAlt size={20} color='#fff' />}
      activeLabel={activeLabel || 'Pin Coordinates'}
      color={color || '#FF8C00'}
      activeMenuControl={{
        controlKey: controlKey || 'PinCoordinates',
        disable: onDisable,
        enable: onEnable,
      }}
    />
  );
};

export default PinCoordinates;
